    rulesDATAPAGAMENTO() {
      const rules = []
      let feePDate = this.editedItem.DATAPAGAMENTOTAXA
      const rule =
        this.editedItem.DATAPAGAMENTOTAXA.length === 10 ||
        (this.editedItem.ESTADO === "FEDERAL" && !feePDate) ||
        (this.editedItem.EMPRESA.includes("Budel") && !feePDate) ||
        (this.editedItem.EMPRESA.includes("Logfit") && !feePDate) ||
        (this.editedItem.SOLICITANTE === "Não Saiu Nada" && !feePDate) ||
        (this.editedItem.BANCO === "Cliente Pagou" && !feePDate) ||
        "Campo Obrigatório"
      rules.push(rule);

      return rules
    },
    rulesBANCO() {
      const rules = [];

      const rule =
        !!this.editedItem.BANCO ||
        this.editedItem.ESTADO === "FEDERAL" ||
        this.editedItem.EMPRESA.includes("Budel") ||
        this.editedItem.EMPRESA.includes("Logfit") ||
        this.editedItem.SOLICITANTE === "Não Saiu Nada" ||
        "Campo Obrigatório"
      rules.push(rule);

      return rules
    },
    rulesCUSTOTAXA() {
      const rules = [];

      const rule =
        !!this.editedItem.CUSTOTAXA ||
        this.editedItem.ESTADO === "FEDERAL" ||
        this.editedItem.EMPRESA.includes("Budel") ||
        this.editedItem.EMPRESA.includes("Logfit") ||
        this.editedItem.SOLICITANTE === "Não Saiu Nada" ||
        "Campo Obrigatório"
      rules.push(rule);

      return rules
    },
    rulesNUMTAXA() {
      const rules = [];

      const rule =
        !!this.editedItem.NUMTAXA ||
        this.editedItem.ESTADO === "FEDERAL" ||
        this.editedItem.ESTADO === "TO" ||
        this.editedItem.ESTADO === "MS" ||
        this.editedItem.ESTADO === "PA" ||
        this.editedItem.EMPRESA.includes("Budel") ||
        this.editedItem.EMPRESA.includes("Logfit") ||
        this.editedItem.SOLICITANTE === "Não Saiu Nada" ||
        "Campo Obrigatório"
      rules.push(rule);

      return rules
    },
    rulesVALORCOBRADO() {
      const rules = [];

      const rule =
        !this.editedItem.VALORCOBRADO ||
        parseFloat(this.editedItem.CUSTOTAXA) <=
          parseFloat(this.editedItem.VALORCOBRADO) ||
        this.editedItem.STATUS === "NÃO COBRAR" ||
        this.editedItem.STATUS === "ACERTO" ||
        "Valor Inferior à Taxa"
      rules.push(rule);

      return rules
    },
    rulesPAGAMENTOBOLETO() {
      const rules = [];

      const rule =
        !this.editedItem.PAGAMENTOBOLETO ||
        this.editedItem.PAGAMENTOBOLETO.length === 10 ||
        (this.editedItem.STATUS !== "PAGO" &&
          this.editedItem.PAGAMENTOBOLETO.length === 10) ||
        "Campo Obrigatório"
      rules.push(rule);

      return rules
