          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
      if (this.editedItem.EMPRESA.includes("Budel")) {
        if (
          this.editedItem.DATAPAGAMENTOTAXA &&
          this.editedItem.DATAPAGAMENTOTAXA !== "null"
        ) {
          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
