        if (
          this.editedItem.DATAPAGAMENTOTAXA &&
          this.editedItem.DATAPAGAMENTOTAXA !== "null"
        ) {
          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
      if (this.editedItem.ESTADO !== "FEDERAL") {
        if (
          this.editedItem.DATAPAGAMENTOTAXA &&
          this.editedItem.DATAPAGAMENTOTAXA !== "null"
        ) {
          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
      if (!this.editedItem.EMPRESA.includes("Budel")) {
        if (
          this.editedItem.DATAPAGAMENTOTAXA &&
          this.editedItem.DATAPAGAMENTOTAXA !== "null"
        ) {
          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
      if (this.editedItem.SOLICITANTE !== "Não Saiu Nada") {
        if (
          this.editedItem.DATAPAGAMENTOTAXA &&
          this.editedItem.DATAPAGAMENTOTAXA !== "null"
        ) {
          this.taxDateDisabled = true
        }
        if (this.editedItem.BANCO && this.editedItem.BANCO !== "null") {
          this.bancoDisabled = true
        }
        if (this.editedItem.CUSTOTAXA && this.editedItem.CUSTOTAXA !== "null") {
          this.custoTaxaDisabled = true
        }
        if (this.editedItem.NUMTAXA && this.editedItem.NUMTAXA !== "null") {
          this.numTaxaDisabled = true
        }
        if (!this.editedItem.DATAPAGAMENTOTAXA) {
          this.taxDateDisabled = false
        }
        if (!this.editedItem.BANCO) {
          this.bancoDisabled = false
        }
        if (!this.editedItem.CUSTOTAXA) {
          this.custoTaxaDisabled = false
        }
        if (!this.editedItem.NUMTAXA) {
          this.numTaxaDisabled = false
        }
      }
    },

    randomPhrase() {
      return (this.fraseAleatoria = Math.floor(
        Math.random() * this.frasesMotivacionais.length
      ))
    },
    setEditTime() {
      const timeElapsed = Date.now()
      const rightNow = new Date(timeElapsed)
      this.timeNow = rightNow.toLocaleString("pt-BR")
    },

    formatDate(tempDate) {
      if (!tempDate) {
        return null
      }

      const [year, month, day] = tempDate.split("-")
      return `${day}/${month}/${year}`
    },
    parseDate(tempDate) {
      if (!tempDate || tempDate.length != 10) {
        return ""
      }

      const [day, month, year] = tempDate.split("/")
      return `${year}-${month.padStart(2, "0")}-${day.padStart(2, "0")}`
    },

    parseMonetary() {
      this.editedItem.CUSTOTAXA = this.editedItem.CUSTOTAXA.replace("R$ ", "")
      this.editedItem.VALORCOBRADO = this.editedItem.VALORCOBRADO.replace(
        "R$ ",
        ""
      )
      this.editedItem.VALORBOLETO = this.editedItem.VALORBOLETO.replace(
        "R$ ",
        ""
      )
      this.editedItem.SALDO = this.editedItem.SALDO.replace("R$ ", "")
    },

    getColor(STATUS) {
      if (STATUS === "PAGO") {
        return "blue lighten-4"
      } else if (STATUS === "A VENCER") {
        return "green lighten-4"
      } else if (STATUS === "TAXA PAGA") {
        return "purple lighten-4"
      } else if (STATUS === "A COBRAR") {
        return "red lighten-4"
      } else if (STATUS === "CONFERIR") {
        return "orange lighten-4"
      } else if (STATUS === "VENCIDO") {
        return "red lighten-4"
      } else if (STATUS === "NÃO COBRAR" || STATUS === "ACERTO") {
        return "red lighten-4"
      } else if (STATUS === "") {
        return ""
      } else {
        return "grey lighten-2"
      }
    },
    getIcon(STATUS) {
      if (STATUS === "PAGO") {
        return "mdi-check-all blue--text"
      } else if (STATUS === "A VENCER" || STATUS === "A DEPOSITAR") {
        return "mdi-cash-clock"
      } else if (STATUS === "TAXA PAGA") {
        return "mdi-check"
      } else if (STATUS === "A COBRAR") {
        return "mdi-clock-alert-outline"
      } else if (STATUS === "CONFERIR") {
        return "mdi-email-alert-outline"
      } else if (STATUS === "VENCIDO") {
        return "mdi-alert red--text"
      } else if (STATUS === "NÃO COBRAR" || STATUS === "ACERTO") {
        return "mdi-cash-remove red--text"
      } else {
        return "mdi-list-status"
      }
    },

    getColor2(STATUSAET) {
      if (STATUSAET === "VALIDA") {
        return "green lighten-4"
      } else if (STATUSAET === "A VENCER") {
        return "orange lighten-4"
      } else if (STATUSAET === "VENCIDA") {
        return "red lighten-4"
      } else if (STATUSAET === "RENOVADA") {
        return "blue lighten-4"
      } else if (STATUSAET === "") {
        return ""
      } else {
        return "grey lighten-2"
      }
    },
    getIcon2(STATUSAET) {
      if (STATUSAET === "VALIDA") {
        return "mdi-check-all green--text"
      } else if (STATUSAET === "A VENCER") {
        return "mdi-alert"
      } else if (STATUSAET === "VENCIDA") {
        return "mdi-clock-alert"
      } else if (STATUSAET === "RENOVADA") {
        return "mdi-check"
      } else {
        return "mdi-list-status"
      }
    },

    fetchLicences() {
      return fetch("https://sheetdb.io/api/v1/afxq0nl1f0gvg")
        .then((r) => r.json())
        .then((r) => {
          this.licences = r
        })
    },

    editItem(item) {
      this.err.includes("Duplicado")
        ? alert("Não atualizar lançamentos e contatar suporte.")
        : ((this.editedIndex = this.licences.indexOf(item)),
          (this.editedItem = Object.assign({}, item)),
          (this.dialog = true))
    },
    deleteItem(item) {
      this.editedIndex = this.licences.indexOf(item)
      this.editedItem = Object.assign({}, item)
      this.dialogDelete = true
    },

    editItemConfirm() {
      // salva edição
      if (this.editedIndex > -1) {
        axios
          .put(
            `https://sheetdb.io/api/v1/afxq0nl1f0gvg/ID/${this.editedItem.ID}`,
            {
              data: {
                SOLICITANTE: `${this.editedItem.SOLICITANTE}`,
                PLACA: `${this.editedItem.PLACA}`,
                NUMTAXA: `${this.editedItem.NUMTAXA}`,
                CUSTOTAXA: `${this.editedItem.CUSTOTAXA}`,
                DATAPAGAMENTOTAXA: `${this.editedItem.DATAPAGAMENTOTAXA}`,
                BANCO: `${this.editedItem.BANCO}`,
                AET: `${this.editedItem.AET}`,
                EMISSÃO: `${this.editedItem.EMISSÃO}`,
                VENCIMENTO: `${this.editedItem.VENCIMENTO}`,
                VALORCOBRADO: `${this.editedItem.VALORCOBRADO}`,
                SALDO: `${this.editedItem.SALDO}`,
                STATUSAET: `${this.editedItem.STATUSAET}`,
                STATUS: `${this.editedItem.STATUS}`,
                NUMBOLETO: `${this.editedItem.NUMBOLETO}`,
                VALORBOLETO: `${this.editedItem.VALORBOLETO}`,
                VENCIMENTOBOLETO: `${this.editedItem.VENCIMENTOBOLETO}`,
                PAGAMENTOBOLETO: `${this.editedItem.PAGAMENTOBOLETO}`,
                OBSERVAÇÃO: `${this.editedItem.OBSERVAÇÃO}`,
                EDICAO:
                  `${this.editedItem.EDICAO}` +
                  ` ${this.user} ${this.timeNow}.`,
              },
            }
          )
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              this.fetchLicences()
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 1500)
            }
          })
          .catch((error) => {
            if (error.response) {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
                window.location.reload()
              }, 1500)
            }
          })
        this.dialogSave = false
        setTimeout(() => {
          this.close()
        }, 200)
      } else {
        this.save()
      }
    },
    deleteItemConfirm() {
      this.licences.splice(this.editedIndex, 1)
      this.closeDelete()
    },

    close() {
      this.count = 0
      this.executed = false
      this.dialog = false
      this.companyExists = true
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },
    closeDialogSave() {
      this.count = 0
      this.dialogSave = false
      this.companyExists = true
    },
    closeDelete() {
      this.count = 0
      this.dialogDelete = false
      this.companyExists = true
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    validateField() {
      this.$refs.form.validate()
    },

    clone() {
      if (this.editedIndex > -1) {
        this.tempEmpresa = this.editedItem.EMPRESA
        this.tempSolicitante = this.editedItem.SOLICITANTE
        this.tempPgBoleto = this.editedItem.PAGAMENTOBOLETO
        this.tempComercial = this.editedItem.COMERCIAL
        this.tempPlaca = this.editedItem.PLACA
        this.tempEstado = this.editedItem.ESTADO
        this.tempStatus = this.editedItem.STATUS
        this.tempDataPgTaxa = this.editedItem.DATAPAGAMENTOTAXA
        this.tempBanco = this.editedItem.BANCO
        this.tempCusto = this.editedItem.CUSTOTAXA
        this.tempDataEmissao = this.editedItem.EMISSÃO
        this.tempDataVencimento = this.editedItem.VENCIMENTO
        this.tempCobrado = this.editedItem.VALORCOBRADO
        this.tempVencimentoBoleto = this.editedItem.VENCIMENTOBOLETO
        this.tempBoleto = this.editedItem.VALORBOLETO
        this.tempNumBoleto = this.editedItem.NUMBOLETO
        this.tempSaldo = this.editedItem.SALDO
        this.tempObs = this.editedItem.OBSERVAÇÃO
        setTimeout(() => {
          this.close()
        }, 300)
        setTimeout(() => {
          document.getElementById("newItemBtn").click()
        }, 300)
        setTimeout(() => {
          this.editedItem.EMPRESA = this.tempEmpresa
          this.editedItem.SOLICITANTE = this.tempSolicitante
          this.editedItem.PAGAMENTOBOLETO = this.tempPgBoleto
          this.editedItem.COMERCIAL = this.tempComercial
          this.editedItem.PLACA = this.tempPlaca
          this.editedItem.ESTADO = this.tempEstado
          this.editedItem.STATUS = this.tempStatus
          this.editedItem.DATAPAGAMENTOTAXA = this.tempDataPgTaxa
          this.editedItem.BANCO = this.tempBanco
          this.editedItem.CUSTOTAXA = this.tempCusto
          this.editedItem.EMISSÃO = this.tempDataEmissao
          this.editedItem.VENCIMENTO = this.tempDataVencimento
          this.editedItem.VALORCOBRADO = this.tempCobrado
          this.editedItem.VENCIMENTOBOLETO = this.tempVencimentoBoleto
          this.editedItem.VALORBOLETO = this.tempBoleto
          this.editedItem.NUMBOLETO = this.tempNumBoleto
          this.editedItem.SALDO = this.tempSaldo
          this.editedItem.OBSERVAÇÃO = this.tempObs
        }, 300)
      } else {
        this.showCloneBtn = false
      }
    },
    saveStatus(item) {
      this.setEditTime()
      this.$nextTick(() => {
        this.editedIndex = this.licences.indexOf(item)
        axios
          .put(
            `https://sheetdb.io/api/v1/afxq0nl1f0gvg/ID/${
              this.editedIndex + 1
            }`,
            {
              data: {
                STATUS: `${item.STATUS}`,
                EDICAO: `${item.EDICAO}` + ` ${this.user}, ${this.timeNow}. `,
              },
            }
          )
          .then((response) => {
            if (response.status === 200 || response.status === 201) {
              this.showUpdate = !this.showUpdate
              this.fetchLicences()
              setTimeout(() => {
                this.showUpdate = !this.showUpdate
              }, 1500)
            } else if (response.status === 429) {
              alert(
                "O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço."
              )
              window.location.reload()
            } else if (
              response.status === 401 ||
              response.status === 403 ||
              response.status === 404
            ) {
              alert("Erro de servidor. Entre em contato com o suporte.")
              window.location.reload()
            }
          })
          .catch((error) => {
            if (error.response) {
              this.showError = !this.showError
              setTimeout(() => {
                this.showError = !this.showError
                window.location.reload()
              }, 1500)
            }
          })
        this.close()
      })
    },

    save() {
      var executed = false
      var form = document.getElementById("sheetdb-form")
      if (this.editedIndex > -1) {
        this.dialogSave = true
      } else {
        // salva novo
        if (this.editedIndex === -1) this.saveCompany()
        if (!this.editedItem.ID)
          alert(
            "lançamento sem ID. Favor contatar suporte antes de prosseguir."
          )
        this.$nextTick(() => {
          if (!executed) {
            executed = true
            fetch(form.action, {
              method: "POST",
              body: new FormData(form),
            })
              .then((response) => {
                if (response.status === 200 || response.status === 201) {
                  this.close()
                  this.showPost = !this.showPost
                  this.fetchLicences()
                  setTimeout(() => {
                    this.showPost = !this.showPost
                  }, 1500)
                } else if (response.status === 429) {
                  alert(
                    "Erro ao Salvar" +
                      "O limite de solicitações foi excedido! 10.000 licenças foram lançadas esse mês! Entre em contato com o suporte para liberar mais espaço."
                  )
                } else {
                  alert(
                    "Erro ao Salvar" +
                      "Erro de servidor. Entre em contato com o suporte."
                  )
                }
              })
              .catch((error) => {
                if (error.response) {
                  this.showError = !this.showError
                  setTimeout(() => {
                    this.showError = !this.showError
                    window.location.reload()
                  }, 1500)
                }
              })
          }
        })
      }
    },
    saveCompany() {
      if (this.companyExists === false) {
        axios.post(`https://sheetdb.io/api/v1/8nmjdq4kt9z0x`, {
          data: {
            IDEMPRESA: `${this.editedItem.IDEMPRESA}`,
            RAZAOSOCIAL: `${this.editedItem.EMPRESA}`,
            CNPJ: `${this.editedItem.CNPJ}`,
          },
        })
      }
    },

    batchItemsUpdateConditionals() {
      let valorCobradoConditional = {
        VALORCOBRADO: `${this.editedItem.VALORCOBRADO}`,
      }
      let numBoletoConditional = { NUMBOLETO: `${this.editedItem.NUMBOLETO}` }
      let statusConditional = { STATUS: `${this.editedItem.STATUS}` }
      let valorBoletoConditional = {
        VALORBOLETO: `${this.editedItem.VALORBOLETO}`,
      }
      let vencimentoBoletoConditional = {
        VENCIMENTOBOLETO: `${this.editedItem.VENCIMENTOBOLETO}`,
      }
      let pagamentoBoletoConditional = {
        PAGAMENTOBOLETO: `${this.editedItem.PAGAMENTOBOLETO}`,
      }
      let obsConditional = { OBSERVAÇÃO: `${this.editedItem.OBSERVAÇÃO}` };

      if (this.editedItem.VALORCOBRADO)
        Object.assign(this.objConditionals, valorCobradoConditional)
      if (this.editedItem.NUMBOLETO)
        Object.assign(this.objConditionals, numBoletoConditional)
      if (this.editedItem.STATUS)
        Object.assign(this.objConditionals, statusConditional)
      if (this.editedItem.VALORBOLETO)
        Object.assign(this.objConditionals, valorBoletoConditional)
      if (this.editedItem.VENCIMENTOBOLETO)
        Object.assign(this.objConditionals, vencimentoBoletoConditional)
      if (this.editedItem.PAGAMENTOBOLETO)
        Object.assign(this.objConditionals, pagamentoBoletoConditional)
      if (this.editedItem.OBSERVAÇÃO)
        Object.assign(this.objConditionals, obsConditional)
    },
    updateBatchItems() {
      this.batchItemsUpdateConditionals()
      setTimeout(() => {
        let batchItemsIds = []
        let obj = {};

        batchItemsIds = this.selected.map((item) => item.ID)
        for (let i = 0; i < this.selected.length; i++) {
          obj = { query: `ID=${batchItemsIds[i]}` };

          Object.assign(obj, this.objConditionals);

          this.batchItems.push(obj)
        }
      }, 200)
    },
    saveBatch() {
      this.updateBatchItems()
      setTimeout(() => {
        this.setEditTime()
        setTimeout(() => {
          axios
            .put(`https://sheetdb.io/api/v1/afxq0nl1f0gvg/batch_update`, {
              data: this.batchItems, // salva as alterações
            })
            .then((response) => {
              if (response.status === 200 || response.status === 201) {
                this.showPost = !this.showPost
                this.fetchLicences()
                setTimeout(() => {
                  this.showPost = !this.showPost
                }, 1500)
              }
            })
            .catch((error) => {
              if (error.response) {
                this.showError = !this.showError
                setTimeout(() => {
                  this.showError = !this.showError
                  window.location.reload()
                }, 1500)
              }
            })
          this.dialogSave = false
          setTimeout(() => {
            this.closeBatch()
          }, 200)
        }, 200)
      })
    },
    closeBatch() {
      this.count = 0
      this.batchDialog = false
      this.$nextTick(() => {
        this.editedItem = Object.assign({}, this.defaultItem)
        this.editedIndex = -1
      })
    },

    dMask(value) {
      console.log(value)
      const day = [
        /[0-3]/,
        /[0-9]/,
        // value.toString().charAt(0) != '3' ? /[0-9]/ : /[0-1]/,
      ]
      const month = [
        /[0-1]/,
        /[0-9]/,
        // value.toString().charAt(3) === '1' ? /[0-2]/ : /[0-9]/,
      ]
      const year = [/[2]/, /[0]/, /[1-2]/, /[0-9]/]
      this.dateMask = [...day, "/", ...month, "/", ...year]
    },
    greaterId() {
      this.count++
      console.log(this.count)
      if (
        this.editedIndex === -1 &&
        (this.count == 5 ||
          this.count == 10 ||
          this.count == 15 ||
          this.count == 20)
      ) {
        axios.get("https://sheetdb.io/api/v1/afxq0nl1f0gvg").then((r) => {
          console.log(r.data.length)
          this.editedItem.ID = r.data.length + 1 + "" + this.userId
        })
      }
    },
    greaterCompanyId() {
      axios.get("https://sheetdb.io/api/v1/8nmjdq4kt9z0x").then((r) => {
        if (this.companyExists === false) {
          this.editedItem.IDEMPRESA = r.data.length + 1
        } else if (this.companyExists === true) {
          const fr = r.data.filter(
            (item) => item.RAZAOSOCIAL === this.editedItem.EMPRESA
          )
          console.log(fr[0].CNPJ)
          this.editedItem.IDEMPRESA = fr[0].IDEMPRESA
          this.editedItem.CNPJ = fr[0].CNPJ
        }
      })
    },
  },
};
</script>

